
.screenshots {
  @include section();

  background-color: $white;

  &__title {
    @include h2();

    margin-bottom: 2rem;
  }

  &__items {
    display: flex;
    flex-direction: column;

    .screenshot {
      margin-bottom: 2.5rem;

      &:last-child {
        margin-bottom: 0;
      }

      @media($desctop) {
        margin-bottom: 0;

        flex-basis: calc(50% - 0.8rem);

        &:nth-child(n + 3) {
          margin-top: 3rem;
        }
      }
    }
    
    @media ($desctop) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
}