

.prices {
  @include section();

  background-color: $white;

  &__title {
    @include h2();
    margin-bottom: 2rem;
  }

  &__items {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media($desctop) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: flex-end;
    }

    .price {
      margin-bottom: 3rem;

      &:last-child {
        margin-bottom: 0;
      }

      @media($desctop) {
        margin-bottom: 0;
      }
    }
  }
}