@mixin h1 ($color: $white) {
    margin-bottom: 1.8rem;

    font-size: 4rem;
    font-family: $mainFont;

    color: $color;
    text-shadow: $mainShadow;

    @media ($tablet) {
        margin-bottom: 3rem;

        font-size: 5.9rem;
    }
}

@mixin h2 ($color: $primaryColor, $align: center) {
    font-size: 3rem;

    color: $color;
    text-align: $align;
    line-height: 1.2;
    font-weight: 400;
}

@mixin h3 ($color: $primaryColor) {
    font-size: 2.4rem;

    color: $color;
    line-height: 1.25;
}

@mixin p1 ($color: $textColor) {
    font-size: 1.6rem;

    color: $color;
    line-height: 1.5;
}

@mixin p2 ($color: $textColor) {
    font-size: 1.6rem;

    color: $color;
    line-height: 1.29;
}

@mixin lead($color: $white) {
    font-size: 2rem;
    line-height: 1.8;

    color: $color;
    text-shadow: $mainShadow;
}

@mixin img($bg: $primaryColor) {
    background-color: $bg;
    object-fit: cover;
    object-position: center;
}

@mixin btn() {
    padding: 1.4rem 4.8rem;
    display: inline-block;

    font-size: 1.8rem;
    font-weight: 700;
    line-height: 1;
    text-align: center;

    cursor: pointer;
    border: none;
    color: $primaryColor;
    background-color: $white;
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 0.5rem;

    &:hover {
        background-color: darken($white, 20%);
    }

    &:focus {
        outline: none;
    }
}

@mixin input() {
    padding: 1.4rem 1rem;

    font-size: 1.6rem;
    line-height: 1;

    color: $secondaryColor;
    background-color: $white;
    border: none;
    border-radius: 0.5rem;

    &::placeholder {
        color: $secondaryColor;
    }
}

@mixin textarea() {
    @include input();
    height: 7rem;
    resize: none;

    font-family: $mainFont;
}

@mixin section($pt: 5rem, $pb: 5rem) {
    padding-top: $pt;
    padding-bottom: $pb;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    
    @media ($projector) {
        padding-left: 0;
        padding-right: 0;
    }
}