
.footer {

  &__main{
    @include section();

    background-color: $primaryColor;
  }

  &__title {
    @include h2($white);
  }

  &__contacts {
    display: flex;
    flex-direction: column;

    @media($desctop) {
      flex-direction: row;
      justify-content: center;
    }
  }

  &__contact-form {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    margin-bottom: 4rem;

    @media($desctop) {
      flex-basis: 50rem;
      margin-bottom: 0;
      margin-right: 4.6rem;
    }
  }

  &__input {
    @include input();

    margin-bottom: 2rem;
  }

  &__textarea {
    @include textarea();

    margin-bottom: 2rem;
  }

  &__submit {
    @include btn();

    align-self: center;
  }

  &__contacts-items {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media($desctop) {
      align-items: flex-start;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    margin-bottom: 3.2rem;
  }

  &__link-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1.8rem;
    &:last-child {
      margin-bottom: 0;
    }

    text-decoration: none;

    img {
      display: block;
      margin-right: 0.5rem;
    }

    span {
      @include p1($white);
    }
  }

  &__copyright{
    padding: 0.8rem 0 0 0.4rem;

    background-color: $lightingGrey;

    &-text {
      font-size: 1.2rem;
      text-align: center;

      color: $secondaryColor;
      letter-spacing: 0.3px;
    }
  }
}