
.about {
  @include section();

  background-color: $white;

  &__wrapper {
    display: flex;
    flex-direction: column-reverse;

    @media ($tablet) {
      flex-direction: row;
    }
  }

  &__title {
    @include h2($align: left);
    margin-bottom: 2rem;
  }

  &__desc {
    @include p1();
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__image {
    @include img();
    height: 24.5rem;
    width: 100%;
    margin-bottom: 1.5rem;
    
    @media ($tablet) {
      width: 25rem;
      margin-bottom: 0;
      margin-left: 5rem;

      flex-shrink: 0;
    }

    @media ($desctop) {
      width: 43rem;
      margin-left: 10rem;
    }
  }
}