
.price {
    width: 30rem;
    padding: 1.6rem 0.2rem 2rem;

    background-color: $primaryColor;
    border-radius: 1rem;

    &__title {
        @include h3($white);
        margin-bottom: 1.7rem;
        
        line-height: 1;
        text-align: center;
    }

    &__sum {
        width: 100%;
        height: 7.1rem;
        padding: 1.8rem 0;
        margin-bottom: 1.5rem;

        font-size: 4rem;
        text-align: center;
        line-height: 1;

        background-color: $white;
        color: $primaryColor;

        &::before {
            content: "$";
        }
    }

    &__features {
       @include p2($white);
       padding-left: 3.3rem;
       padding-right: 3.6em;
       margin-bottom: 1.5rem;
       
       line-height: 1.71;
    }

    &__btn {
        @include btn();
        display: block;
        margin: 0 1.8rem;
    }
}