

.stage {
    @include section();

    background-color: $primaryColor;

    &__wrapper {
        display: flex;
        flex-direction: column-reverse;

        @media ($tablet) {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__title {
        @include h1();
    }

    &__list {
        @include lead();

        &-item {
            list-style: url("../images/pngwing.com.png");
        }
    }

    &__image {
        @include img($white);
        width: 100%;
        height: 21.4rem;

        flex-shrink: 0;

        @media ($tablet) {
            padding-left: 0.5rem;
            max-width: 30rem;
            margin-left: 4rem;
        }

        @media ($desctop) {
            padding-left: 0.5rem;
            max-width: 46.9rem;
            margin-left: 8rem;
        }
    }
}