
.review {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media ($tablet) {
        flex-direction: row;
        align-items: flex-start
    }

    &__image {
        width: 7.5rem;
        height: 7.5rem;
        margin-bottom: 2.6rem;

        border-radius: 50%;

        @include img();

        @media($tablet) {
            margin-bottom: 0;
            margin-right: 2.7rem;

            flex-shrink: 0;
        }
    }

    &__text {
        padding: 1.6rem 2rem 1.2rem 1.5rem;

        @include p2();

        background-color: $lightGrey;
        border-radius: 1rem;
       
        position: relative;

        &::before {
            content: '';
            width: 2rem;
            height: 2rem;
            background-color: $lightGrey;
            position: absolute;
            top: -1rem;
            left: calc(50% - 1rem);
            transform: rotate(45deg);

            @media ($tablet) {
                top: 2.8rem;
                left: -1rem;
            }
        }

        &-item {
            margin-bottom: 0.8rem;
            font-style: italic;
        }

        &-author {
            color: $grey;
        }
    }
}